.header {
  position: absolute;
  left: 0%;
  top: 0%;
  transform: translate(-0%, -0%);
  display: block;
  z-index: 1000;
  width: 100%;
}

.navbar {
  font-family: 'Cormorant Garamond', serif;
  font-size: 1.3em;
  font-weight: 500;

  ul {
    list-style-type: none;
    margin: 2em 3em;
    padding: 0;
    overflow: hidden;
  }

  li {
    float: left;
  }

  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 0.5em 0.5em;
    text-decoration: none;
    display: inline-block;
    position: relative;
  }

  li a::after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 0.05em;
    bottom: 0;
    left: 0;
    background-color: #fff;
    transform-origin: bottom center;
    transition: transform 0.25s ease-out;
  }

  li a:hover::after {
    transform: scaleX(1);
    transform-origin: bottom center;
  }
}
