.splash {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.1)),
    url('./assets/proposal-1.jpg');
}

.names {
  position: absolute;
  color: #fff;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  margin: 0;
  text-align: center;
  font-family: 'Merriweather', serif;
  font-weight: 300;
}

.events {
  padding: 4em 0;
}

.subtitle {
  font-family: 'Corinthia', cursive;
  font-weight: 100;
  color: white;
  height: 8em;
  width: 17em;
  position: absolute;
  left: 50%;
  top: 20%;
  transform: translate(-50%, -35%);
  font-size: 1.5rem;
  margin: 0;

  p {
    font-family: 'Corinthia', cursive;
    color: white;
    margin: 0 auto;
  }

  .the {
    font-size: 3em;
    position: absolute;
    top: 12.5%;
    left: 0%;
  }

  .wedding {
    font-size: 5em;
    position: absolute;
    top: 30%;
    left: 12.5%;
  }

  .of {
    font-size: 3em;
    position: absolute;
    top: 80%;
    right: 0%;
  }
}

@media only screen and (max-width: 520px) {
  .names {
    font-size: 2.5em;
  }

  .subtitle {
    height: 6em;
    width: 13em;
    position: absolute;
    left: 50%;
    top: 25%;
    transform: translate(-50%, -35%);

    .the {
      font-size: 2em;
      position: absolute;
      top: 20%;
      left: 0%;
    }

    .wedding {
      font-size: 4em;
      position: absolute;
      top: 30%;
      left: 12.5%;
    }

    .of {
      font-size: 2em;
      position: absolute;
      top: 90%;
      right: 0%;
    }
  }
}
