.event {
  p {
    margin: 0;
  }

  .datetime {
    float: left;
  }
  .datetime {
    .date {
      font-size: 1.5em;
    }
    .time {
      font-size: 1.25em;
    }
  }

  .eventDetails {
    .title {
      font-size: 1.5em;
      font-weight: 600;
    }

    .description {
      font-size: 1.25em;
      margin-bottom: 1.25em;
    }
  }

  img {
    margin: 0.5em 0.5em 0.5em 0;
  }

  .dresscode {
    display: flex;
    align-items: center;

    .description {
      font-size: 1.2em;
    }
  }

  .address {
    display: flex;
    align-items: center;
    font-size: 1.2em;
  }

  .divider {
    border-top: 0.05em solid #1a1a1a;
    margin: 1em 0em;
  }
}
